body {
  margin: 0;
  padding: 0;
  background-color: white;
  font-family: sans-serif;
  font-size: 16px;
  line-height: 1.4;
  color: #333;
}

/*layout*/
/*top-header*/
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
.row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.App {
  text-align: center;
}

/* Properties */

#dhead {
  margin-top: 20px;
}

#dpic {
  grid-column: span 6;
  text-align: right;
}
#ddesc {
  padding-top: 40px;
  padding-left: 20px;
  grid-column: span 6;
  text-align: left;
}
@media (max-width: 991px) {
  #dpic {
    grid-column: span 12;
    text-align: center;
  }
  #ddesc {
    grid-column: span 12;
    padding-top: 20px;
    text-align: center;
  }
}
h1 {
  font-size: 34px;
  font-weight: normal;
  padding: 0;
  margin: 0;
}
h2 {
  font-weight: normal;
  font-style: italic;
  color: #999;
  font-size: 18px;
  padding: 0;
  margin: 5px 0 10px 0;
}
@media (min-width: 992px) {
  h2 {
    max-width: 300px;
  }
}
#dpic img {
  width: 240px;
  height: 240px;
  border-radius: 120px;
}
.iico {
  width: 40px;
  height: 40px;
  padding-right: 3.5px;
}

/*-------------------------------------------------------------------------------------------*/
/*--------------------------history---------------------------------------------f*/

#history {
  margin-top: 20px;
}
#history a {
  color: blue;
}
.timespan {
  grid-column: span 1;
  font-size: 14px;
  text-align: right;
  padding-right: 5px;
  color: #bbb;
}
.ico {
  grid-column: span 1;
  vertical-align: top;
  border-left: 2px solid #cfcfcf;
  position: relative;
}
.ico img {
  border-radius: 5px;
  width: 100%;
  max-width: 80px;
  margin-left: 10px;
}


.desc {
  grid-column: span 10;
  vertical-align: top;
  font-size: 17px;
  padding-left: 20px;
  padding-bottom: 20px;
  text-align: left;
}
.entry {
}
.entry-dot {
  position: absolute;
  top: 0px;
  left: -8px;
  width: 10px;
  height: 10px;
  border-radius: 7px;
  background-color: #cfcfcf;
  border: 2px solid white;
}
